@import url(https://fonts.googleapis.com/css2?family=Amiri:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{font-size:2.5rem;color:#746c69;font-family:'Amiri', serif}.navbar-light .navbar-nav .nav-link{color:rgba(0,0,0,0.5);font-family:'Amiri', serif}.nav-link:hover{font-size:large;transition-duration:2s}.navbar-light .navbar-brand{color:rgba(0,0,0,0.9);font-family:'Amiri', serif}

footer .main-footer{padding:20px 0;background:#00434C}footer ul{padding-left:0;list-style:none}.footer-copyright{background:#7DA19D;padding:5px 0}.footer-copyright .logo{display:inherit}.footer-copyright nav{float:right;margin-top:5px}.footer-copyright nav ul{list-style:none;margin:0;padding:0}.footer-copyright nav ul li{border-left:1px solid #505050;display:inline-block;line-height:12px;margin:0;padding:0 8px}.footer-copyright nav ul li a{color:#969696}.footer-copyright nav ul li:first-child{border:medium none;padding-left:0}.footer-copyright p{color:#f9f9f9;margin:2px 0 0}.footer-top{background:#252525;padding-bottom:30px;margin-bottom:30px;border-bottom:3px solid #222}footer.transparent .footer-top,footer.transparent .main-footer{background:transparent}footer.transparent .footer-copyright{background:none repeat scroll 0 0 rgba(0,0,0,0.3)}footer.light .footer-top{background:#f9f9f9}footer.light .main-footer{background:#f9f9f9}footer.light .footer-copyright{background:none repeat scroll 0 0 rgba(255,255,255,0.3)}.footer- .logo{display:inline-block}.widget{padding:20px;margin-bottom:40px}.widget.widget-last{margin-bottom:0px}.widget.no-box{padding:0;background-color:transparent;margin-bottom:40px;box-shadow:none;-webkit-box-shadow:none;-moz-box-shadow:none;-ms-box-shadow:none;-o-box-shadow:none}.widget.subscribe p{margin-bottom:18px}.widget li a{color:#F7BE16}.widget li a:hover{color:#4b92dc}.widget-title{margin-bottom:20px}.widget-title span{background:#839FAD none repeat scroll 0 0;display:block;height:1px;margin-top:25px;position:relative;width:20%}.widget-title span::after{background:inherit;content:"";height:inherit;position:absolute;top:-4px;width:50%}.widget-title.text-center span,.widget-title.text-center span::after{margin-left:auto;margin-right:auto;left:0;right:0}.widget .badge{float:right;background:#7f7f7f}.typo-light h1,.typo-light h2,.typo-light h3,.typo-light h4,.typo-light h5,.typo-light h6,.typo-light p,.typo-light div,.typo-light span,.typo-light small{color:#fff}ul.social-footer2{margin:0;padding:0;width:auto}ul.social-footer2 li{display:inline-block;padding:0}ul.social-footer2 li a:hover{background-color:#F7BE16}ul.social-footer2 li a{display:block;height:30px;width:30px;text-align:center}.btn{background-color:#F7BE16;color:#fff}.btn:hover,.btn:focus,.btn.active{background:#99a5b1;color:#fff;-ms-box-shadow:0 15px 30px rgba(0,0,0,0.1);-o-box-shadow:0 15px 30px rgba(0,0,0,0.1);box-shadow:0 15px 30px rgba(0,0,0,0.1);transition:all 250ms ease-in-out 0s}

.carousel{position:relative;display:block}.carousel-item.active{display:block;text-align:-webkit-center}

.mainFinance{background:url(/EMS-Front/static/media/finance1.8fa905f9.jpg) no-repeat center center fixed;background-size:cover;display:flex;justify-content:center;flex-direction:column;align-items:center;text-align:center}#card{display:flex;justify-content:center;flex-direction:column}.MuiCardActions-root{justify-content:center}#Avatar{width:75px;height:75px}

